import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class AppConfigService {
  private appConfig;
  private baseAppUrl: string = "http://sg.mgkslt.com"

  constructor(private http: HttpClient) { }

  loadAppConfig() {
    return this.http.get(this.baseAppUrl + '/secure/user/listRoles')
      .toPromise()
      .then(data => {
        this.appConfig = data
        console.log(data);
      });
  }

  getConfig() {
    return this.appConfig;
  }
}


